import Swiper, { Navigation, Pagination } from 'swiper';

window.onload = () => {  //    
    if (window.matchMedia('(max-width: 767px)').matches) { // //     ,      767px
        const swiper = new Swiper('.swiper', {
            modules: [Navigation, Pagination],
            direction: 'horizontal', //   Swiper
            loop: true,  //  Swiper  (Swiper        .swiper-wrapper    "" )
            slidesPerView: 'auto', //     ,    -     .swiper-wrapper
            pagination: { //  
                el: '.swiper-pagination', //   
                clickable: true //  ,    bullets 
            },
            init: true
        })
    }
}